.message-input {
  height: 60px;
  display: flex;
  border-radius: 5px;

  box-shadow: 0px -2px 10px -1px rgba(0, 0, 0, 0.2);
  align-items: center;
  justify-content: space-between;
  input {
    height: 20px;
    width: 375px;
    border: none;
    font-size: 14px;
    letter-spacing: 0.5px;
    outline: none;
    margin-left: 30px;
    &::placeholder {
      color: #caced9;
    }
  }
  .fa-smile,
  .fa-paperclip {
    font-size: 25px;
    color: #caced9;
    cursor: pointer;
    &:hover {
      color: gray;
    }
  }
  .send {
    margin-right: 20px;
    box-shadow: 0px 0px 10px -1px rgba(0, 0, 0, 0.2);
    &:hover {
      box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
      cursor: pointer;
    }
    .fa-telegram-plane {
      color: #0ad69c;
      border-radius: 7px;
      font-size: 28px;
      padding: 5px;
    }
  }
}
