.search-bar {
  display: flex;
  height: 50px;
  align-items: center;
  border-bottom: 1px solid #f5f6f9;
  img {
    margin: 15px;
  }
  .search-input {
    width: 500px;
    border: none;
    outline: none;
    padding: 15px;
    padding-left: 0;
    color: black;
    font-size: 14px;
    letter-spacing: 0.5px;
    &::placeholder {
      color: #caced9;
      font-size: 14px;
    }
  }
}
