.person-chat {
  display: flex;
  width: 300px;
  height: 91px;
  position: relative;
  align-items: center;
  &.read {
    background: #f7f9fc;
  }
  &:hover {
    box-shadow: 0px 5px 14px -2px rgba(0, 0, 0, 0.2);
    cursor: pointer;
  }
  img {
    margin: 22px;
    margin-right: 14px;
    border-radius: 50%;
    width: 50px;
    height: 50px;
  }
  .name-and-text {
    margin: 26px 0;
    p {
      margin: 0;
      &.text {
        font-size: 14px;
        color: #9ba0ad;
      }
    }
    .name {
      font-size: 16px;
      font-weight: 700;
      position: relative;
      display: inline-flex;
      align-items: center;
      width: auto;
      .online-dot {
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background: #0ad69c;
        position: absolute;
        top: 0;
        right: -10px;
      }
      .fa-volume-mute {
        position: absolute;
        font-size: 13px;
        right: -30px;
        color: #9ba0ad;
      }
    }
  }
  .time {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 12px;
    color: #9ba0ad;
  }
}
