.route-and-user {
  width: 944px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  .route {
    display: flex;
    list-style: none;
    margin-left: 40px;
    padding: 0;
    li {
      font-size: 13px;
      margin-right: 14px;
      cursor: pointer;
      &::after {
        margin-left: 12px;
        content: '>';
        color: #0ad69c;
        font-weight: 700;
      }
      &:last-child::after {
        content: '';
      }
    }
  }
  .user {
    display: flex;
    align-items: center;
    img {
      margin-right: 10px;
    }
    p {
      font-size: 14px;
      &::after {
        display: inline-block;
        content: '>';
        transform: rotate(90deg);
        margin-left: 7.5px;
      }
    }
  }
}
