.user-message {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  margin-bottom: 20px;
  img {
    margin-left: 20px;
    margin-right: 11px;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: grey;
  }
}
.my-message {
  margin-right: 20px;
  margin-bottom: 20px;

  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.message-text {
  position: relative;
  width: 300px;
  background: #f7f9fc;
  border-radius: 5px;
  p {
    font-size: 14px;
    color: #222428;
    margin: 0;
    padding: 12px 14px;
    width: 230px;
    &.message-time {
      font-size: 12px;
      color: #9ba0ad;
      position: absolute;
      width: 40px;
      bottom: 0px;
      right: 0px;
    }
  }
}
